import HTTP from '@/httpapi';
import Paginate from '@lib/paginate';
import { cacheReferentiel } from '@lib/cache';

const Ranks = {
    established (gameId, page, limit) {
        return Paginate(HTTP.convert('/v1/game/' + gameId + '/rank/established', 'rankings'), page, { limit: limit }, { cache: cacheReferentiel });
    },
};

export default Ranks;



